document.addEventListener('DOMContentLoaded', function () {
  lbt()
})
function lbt () {
  const lbotu = document.querySelectorAll('.home_head_bg div') // 获取所有的 div 元素
  const lbotucss = ['bg1', 'bg2', 'bg3', 'bg4'] // 背景类的数组
  // 设置轮播图更新的函数
  function updateCarousel () {
    lbotu.forEach((div, index) => {
      // 移除当前 div 的所有可能的背景类
      lbotucss.forEach(css => {
        div.classList.remove(css)
      })
      // 计算新的类索引
      const newCssIndex = (index + lbotunav) % lbotucss.length
      // 为 div 添加新的背景类
      div.classList.add(lbotucss[newCssIndex])
    })
    // 更新全局背景索引
    lbotunav = (lbotunav + 1) % lbotucss.length
  }
  let lbotunav = 0 // 用于跟踪背景类的全局索引
  setInterval(updateCarousel, 4000) // 设置定时器，每3秒更新一次
  updateCarousel() // 初始调用一次以设置初始背景
}
